/* -----------------PRIMARY - FOR SMARTPHONE -------------------*/

.modal-buy{
    position: absolute;
    top: 50%;
    left: 50%;

    height: 502px;

    padding: 0px 20px;

    transform: translate(-50%,-50%);

    background-color: var(--third-bg-color);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 
                0px 1px 1px rgba(0, 0, 0, 0.14), 
                0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 4px;

    &__list{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 40px;

        padding-top: 60px;
    }

    &__item{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 10px;

        padding-bottom: 40px;

        &:first-child{
            border-bottom: 1px solid var(--third-text-color);
        }
    }

