.ani {
  animation-name: ani;
  animation-duration: 1.5s;
}

@keyframes ani {
  0% {
    opacity: 0;
    transform: translateX(-150px);
  }

  60% {
    opacity: 1;
    letter-spacing: .3em;
    transform: translateX(0);
  }

  100% {
    letter-spacing: 0;
    transform: translateX(0);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(360deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.fade-hero {
  animation-name: fade-hero;
  animation-duration: 1.5s;
}

@keyframes fade-hero {
  0% {
    opacity: 0;
    transform: translateX(-500px);
  }

  50% {
    opacity: .3;
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

